import { Container, Row, Col, Button, Modal } from "react-bootstrap";
import { Link } from "react-router-dom";
import {AssessmentCheckModal, ContactForm, LoginForm} from "../components";
import { useState, useEffect } from "react";
import {useDispatch} from "react-redux";

const ourApproachItems = [
    {
        'title': 'Career Assesment & Mapping',
        'text': 'Get yourself assessed by a proprietary Career Quotient test to know your Career Path.',
        'imageSrc': 'assets/svgs/career-assesment-and-mapping.svg',
    },
    {
        'title': 'Course Recommendation',
        'text': 'Based on One’s Personality traits, Get a Right Course Recommendation.',
        'imageSrc': 'assets/svgs/course-recommendation.svg',
    },
    {
        'title': 'Career Guidance',
        'text': 'Get mentored by Career Experts and take timely decisions.',
        'imageSrc': 'assets/svgs/career-guidance.svg',
    },
    {
        'title': 'Gamification To Engage Users',
        'text': 'Gamified platform to reward users on the platform who are active on the platform.',
        'imageSrc': 'assets/svgs/gamification-to-engage-users.svg',
    },
    {
        'title': 'Part/Full Time Opportunities',
        'text': 'Discover a Right Opportunity based on your profile.',
        'imageSrc': 'assets/svgs/partfull-time-opportunities.svg',
    }
]

const BuiltFor = [
    {
        title: 'Individual',
        text: 'CQ is a platform that lets you assess yourself in the most efficient way possible. We suggest career paths, access to different career opportunities, and access to a bunch of educational skill based courses that can help a person decide what’s the right place to invest.'
    },
    {
        title: 'Enterprises',
        text: 'CQ is a talent discovery platform for your enterprise. CQ lets you assess your new recruits by benchmarking against your existing workforce. We partner with you and understand your hiring and talent needs and help you maximize your human potential.'
    },
    {
        title: 'Educational Institutions',
        text: 'CQ is an assessment platform for your institutions. CQ lets your students be discoverable for right job opportunities and internships by marquee companies. We collaborate with you to make your students more hire-able and match them with the right opportunities.'
    },
]

const theProblemItems = [
    {
        'title': 'Clearer Career paths',
        'text': '7 in 10 students are confused about what career path to pursue.',
        'imageSrc': 'assets/svgs/clearer-career-paths.svg',
    },
    {
        'title': 'Improved Career Adoption',
        'text': '61% Millennials would consider switching careers as they feel its not appropriate',
        'imageSrc': 'assets/svgs/improved-career-adoption.svg',
    },
    {
        'title': 'Better Career Guidance',
        'text': 'Only 1% colleges in India have meaningful career guidance support to help students take well informed decision',
        'imageSrc': 'assets/svgs/better-career-guidance.svg',
    },
    {
        'title': 'Superior Decision Tools',
        'text': 'Most students lack a Reliable career decision making framework and relevant course discovery tools',
        'imageSrc': 'assets/svgs/superior-decision-tools.svg',
    },

]

const Homepage = (props) => {
    const [images, setImages] = useState([])

    const importAll = (r) => {
        return r.keys().map(r);
    }
    useEffect(() => {
        setImages(importAll(require.context('../assets/svgs/company-logos', false, /\.(png|jpe?g|svg)$/)))
    }, []);

    return (
        <div className="homepage">
            <Hero />
            <Container className="bg-white">
                <Row className="customers justify-content-around">
                    {images.map((image, index) => (<Col xs={4} md={2} className="text-center my-2 d-flex">
                        <a href="#" className="m-auto"><img className="img-fluid m-auto" key={index} style={{ maxHeight: 50 + 'px' }} src={image.default} alt="" /></a>
                    </Col>))}
                </Row>
            </Container>
            <div id="what-we-build" className="bg-white">
                <WhatWeBuild />
            </div>
            <TheProblem />
            <div className="bg-white border-top-curved">
                <div id="our-approach" >
                    <OurApproach />
                </div>
                <Contact />
                <Footer />
                <hr className="mt-0"/>
                <p className="text-center">© Career Quotient 2021. All rights reserved</p>
            </div>
        </div>
    )
};

const Footer = (props) => {
    return (
        <div className="footer py-4">
            <Row className="justify-content-around">
                <Col className="start-footer mb-4 mb-md-0" md={3} lg={2}>

                    <img className="img-fluid" src="assets/CareerQuotient.png" alt="CareerQuotient Logo" />
                </Col>
                <Col xs={6} md={3} lg={2} className="mb-4 mb-md-0">
                    <ul className="list-unstyled">
                        <li className="mb-4"><a>Home</a></li>
                        <li className="mb-4"><a>Take CQ</a></li>
                        <li className="mb-4"><a>About Us</a></li>
                        <li className="mb-4"><a>Login</a></li>
                    </ul>
                </Col>
                <Col xs={6} md={3} lg={2} className="mb-4 mb-md-0">
                    <div>
                        <p>Solutions -</p>
                        <ul className="list-unstyled">
                            <li className="mb-4 text-secondary">- Individuals</li>
                            <li className="mb-4 text-secondary">- Enterprises</li>
                            <li className="mb-4 text-secondary">- Educational Institutions</li>
                        </ul>
                    </div>
                </Col>
                <Col md={3} lg={2}>
                    <div>
                        <p>Contact Us -</p>
                        <p className="text-secondary">info@careerquotient.com</p>
                    </div>
                    <br />
                    <div>
                        <p>Connect -</p>
                        <ul className="list-inline">
                            <li className="list-inline-item"><a className="text-secondary" href="">Linkedin</a></li>
                            <li className="list-inline-item"><a className="text-secondary" href="">Facebook</a></li>
                            <li className="list-inline-item"><a className="text-secondary" href="">Instagram</a></li>
                        </ul>
                    </div>
                </Col>
            </Row>

        </div>
    )
};


const Hero = (props) => {
    const dispatch = useDispatch();

    return (
        <div className="hero-wrapper">

            <section className="hero">
                <Row>
                    <Col xl={5}>
                        <h1 className="font-weight-bold mb-4">Lightspeed Career Progress Starts Now</h1>
                        <p className="text-landing">Take the Career Quotient Assessment to understand yourself better, and find the right courses and opportunities to take your career to the next level.</p>
                        <div onClick={() => dispatch({type: 'openAssessmentCheckModal'})} className="btn btn-lg btn-primary font-weight-bold" to='/assessment' >Take CQ Assessment</div>
                    </Col>
                    <Col className="flex-center">
                        <img class="img-fluid" src="assets/svgs/hero.png" alt="" />
                   </Col>
                </Row>
            </section>
        </div>
    )
};

const Contact = (props) => {
    return (
        <Container fluid id="contact-us">
            <Row style={{ width: '100%' }} className="my-5 my-xl-0 justify-content-around">
                <Col lg={5} xl={3}>
                    <small>Contact Us</small>
                    <h1 className="font-weight-bold">Interested In Knowing More?</h1>
                </Col>
                <Col xl={{ span: 5, offset: 3 }} lg={{ span: 7, offset: 0 }}>
                    <ContactForm />

                </Col>
            </Row>
        </Container>
    )
};

const TheProblem = () => {
    return (
        <Container fluid id="the-problem" className="my-5 my-xl-0">
            <div className="text-center landing-section-header mt-5 mt-xl-0">
                <small>The Problem</small>
                <h1 className="font-weight-bold">What are we trying to solve?</h1>
            </div>
            <Row noGutters className="justify-content-around">
                {theProblemItems.map((item) => <Col xl={2} className="text-center">
                    <img className="mb-5" height="128" width="128" src={item.imageSrc} />
                    <p className="font-weight-bold">{item.title}</p>
                    <p className="text-secondary">{item.text}</p>
                </Col>)}
            </Row>
        </Container>
    )
}

const OurApproach = (props) => {
    return (
        <Container id="our-approach" fluid>
            <Row className="justify-content-center mt-5 mt-xl-0">
                <Col className="text-lg-center landing-section-header" xl={8}>
                    <small>Our Approach</small>
                    <h1 className="font-weight-bold m-auto">Creating a Complete Ecosystem for Career Guidance</h1>
                </Col>
            </Row>
            <Row className="justify-content-around">
                {ourApproachItems.map((item) => <Col xs={12} sm={6} md={4} lg={3} xl={2} className="bg-dark-circle mx-2 mb-4">
                    <div>
                        <img className="mb-5" height="100" width="100" src={item.imageSrc} />
                        <p className="font-weight-bold">{item.title}</p>
                        <p className="text-secondary">{item.text}</p>
                    </div>
                </Col>)}
                {/* <div className="overlay-line">
                    <img src="assets/svgs/line.svg" />
                </div> */}
            </Row>
        </Container>

    )
};

const WhatWeBuild = (props) => {
    return (
        <Container fluid>
            <div className="landing-section-header mt-5">
                <small>WE BUILD THE PATHWAY TO YOUR DREAMS</small>
                <h1 className="font-weight-bold">Career Quotient Was Built For</h1>
            </div>
            <div className="fill-wrapper">
                <Row className="justify-content-around">
                    {BuiltFor.map((item, index) => <Col className="fill px-4" md={12} lg={6} xl={4}>
                        <Row className="align-items-center mb-3">
                            <p className="fill-leading">{`0${index + 1}`}</p>
                            <h3 className="h3 font-weight-bold">{item.title}</h3>
                        </Row>
                        <p>{item.text}</p>
                        <a style={{ color: "white" }}>
                            <img className="mr-2" height='40' width='40' src="assets/svgs/arrow-circle-right-solid.svg"/>Learn More
                        </a>
                    </Col>)}
                </Row>
            </div>

        </Container>
    )
};


export default Homepage;